@import url(https://fonts.googleapis.com/css?family=Heebo:400,700|Open+Sans:400,700);
@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Cinzel:400,700&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Screen reader only */
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0,0,0,0);
  border: 0;
}

/* Button styling */
.menu-toggle {
  display: inline-block;
  padding: .75em 15px;
  line-height: 1em;
  font-size: 1em;
  color: #333;
  cursor: pointer;
}

.menu-toggle:hover,
#main-menu-checkbox:focus ~ header .menu-toggle {
  color: #c00;
  outline: auto;
}

#main-menu-checkbox {
  position: absolute;
  left: -200px; /* get it off the screen */
}

/*
 Default styles + Mobile first
 Offscreen menu style
*/
.main-menu {
  position: absolute;
  left: -200px;
  top: 0;
  /* height: 100%; */
	/* overflow-y: scroll; */
	overflow-x: visible;
	-webkit-transition: left 0.3s ease,
				      box-shadow 0.3s ease;
	transition: left 0.3s ease,
				      box-shadow 0.3s ease;
	z-index: 999;
}

.main-menu ul {
  list-style: none;
  margin: 0;
  padding: 2.5em 0 0;
  /* Hide shadow w/ -8px while 'closed' */
  box-shadow: -8px 0 8px rgba(0,0,0,.5);
  /* min-height: 100%; */
  width: 200px;
  background: #1a1a1a;
}

.main-menu a,
.main-menu .menu-close {
  display: block;
  padding: .75em 15px;
  line-height: 1em;
  font-size: 1em;
  color: #fff;
  text-decoration: none;
  border-bottom: 1px solid #383838;
}

.main-menu li:first-child a {
  border-top: 1px solid #383838;
}

.main-menu a:hover,
.main-menu a:focus,
.main-menu .menu-close:hover,
#main-menu-checkbox:focus ~ header .menu-close {
  background: #333;
  text-decoration: underline;
}

.main-menu .menu-close {
  position: absolute;
  right: 0;
  cursor: pointer;
}

/*
 On small devices, allow it to toggle...
*/
/*
 :target for non-JavaScript
 aria-expanded="true/false" will be for JavaScript
*/
#main-menu-checkbox:checked ~ header .main-menu,
.main-menu[aria-expanded="true"] {
  left: 0;
  outline: none;
  box-shadow: 3px 0 12px rgba(0,0,0,.25);
}

#main-menu-checkbox:checked ~ header .menu-close,
.main-menu[aria-expanded="true"] .menu-close {
  z-index: 1001;
}

#main-menu-checkbox:checked ~ header .main-menu ul,
.main-menu[aria-expanded="true"] ul {
  position: relative;
  z-index: 1000;
}

/* 
 We could us `.main-menu:target:after`, but
 it wouldn't be clickable.
*/
#main-menu-checkbox:checked ~ header .backdrop,
.main-menu[aria-expanded="true"] + .backdrop {
  position: absolute;
  display: block;  
  content: "";
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 998;
  background: #000;
  background: rgba(0,0,0,.5);
  cursor: default;
}

@supports (position: fixed) {
  .main-menu,
  #main-menu-checkbox:checked ~ header .backdrop,
  .main-menu[aria-expanded="true"] + .backdrop {
    position: fixed;
  }
}

/*
 Larger screen styling
 Horizontal menu
*/
@media (min-width: 768px) {
  .menu-toggle,
  .main-menu .menu-close,
  #main-menu-checkbox {
    display: none;
  }
  
  /* Undo positioning of off-canvas menu */
  .main-menu {
    position: relative;
		left: auto;
		top: auto;
		height: auto;
    overflow-y: auto; /* fix dumb MS Edge scrollbar issue */
  }
  
  .main-menu ul {
    display: flex;
    
    /* Undo off-canvas styling */
    padding: 0;
    box-shadow: none;
    height: auto;
    width: auto;
    background: none;
  }
  
  .main-menu a {
    color: #06c;
    border: 0 !important; /* Remove borders from off-canvas styling */
  }
  
  .main-menu a:hover,
  .main-menu a:focus {
    background: none; /* Remove background from off-canvas styling */
    color: #c00;
  }
}

/*
 Demo purposes only
*/
*,
*:before,
*:after {
  box-sizing: border-box;
}

header {
  align-content: flex-start;
  background-color: white;
  box-shadow: 0 0 3em -1em rgba(255,66,22,.5);
  display: flex;
  justify-content: space-between;
  padding: 0.5em;
}

.logo {
  margin: 0 30px 0 10px;
  font-size: 1.5em;
  text-decoration: none;
}
:root {
  --color: #3c3163;
  --transition-time: 0.5s;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  min-height: 100vh;
  font-family: 'Open Sans';
  background: #fafafa;
}

a {
  color: inherit;
}

.cards-wrapper {
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 3rem;
  padding: 3rem;
  margin: 0 auto;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}

.card {
  font-family: 'Heebo';
  flex-direction: column;
  --bg-filter-opacity: 0.5;
  height: 20em;
  width: 15em;
  font-size: 1.5em;
  color: #3c3163;
  color: var(--color);
  border-radius: 1em;
  padding: 1em;
  display: flex;
  align-items: stretch;
  background-size: cover;
  background-position: center;
  box-shadow: 0 0 5em -1em rgba(256,66,22,.2);
  -webkit-transition: all, 0.5s;
  transition: all, 0.5s;
  -webkit-transition: all, var(--transition-time);
  transition: all, var(--transition-time);
  position: relative;
  overflow: hidden;
  border: 2px solid #ccc;
  text-decoration: none;
}

.card:hover {
  -webkit-transform: rotate(0);
          transform: rotate(0);
}

.card h1 {
  margin: 0;
  font-size: 1.5em;
  line-height: 1.2em;
}

.card p {
  font-size: 0.75em;
  font-family: 'Open Sans';
  margin-top: 0.5em;
  line-height: 2em;
}

.card .tags {
  margin-top: auto;
}

.card .tags .tag {
  font-size: 0.75em;
  background: #3c3163;
  background: var(--color);
  color: white;
  
  border-radius: 0.3rem;
  padding: 0 0.5em;
  margin: 0.5em 0 0 0;
  line-height: 1.5em;
  -webkit-transition: all, 0.5s;
  transition: all, 0.5s;
  -webkit-transition: all, var(--transition-time);
  transition: all, var(--transition-time);
  width: 100%;
}

.card:hover .tags .tag {
  background: rgba(0,0,0,.5);
  color: white;
}

.card:before, .card:after {
  content: '';
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-transform-origin: top left;
          transform-origin: top left;
  border-radius: 50%;
  position: absolute;
  left: -50%;
  top: -50%;
  z-index: -5;
  -webkit-transition: all, 0.5s;
  transition: all, 0.5s;
  -webkit-transition: all, var(--transition-time);
  transition: all, var(--transition-time);
  -webkit-transition-timing-function: ease-in-out;
          transition-timing-function: ease-in-out;
}

.card:before {
  background: white;
  width: 200%;
  height: 200%;
}

.card:after {
  background: rgba(0,0,0,.5);
  width: 250%;
  height: 250%;
}

.card:hover {
  color: white;
}

.card:hover:before, .card:hover:after {
  -webkit-transform: scale(1);
          transform: scale(1);
}

.projects {
  font-size: 2em;
  display: flex;
  height: 3em;
  flex-direction: column;
  text-align: center;
}

.projects h1 {
  font-size: 1em;
  font-weight: normal;
  display: inline-block;
  margin-bottom: 0px;
}

.instruction {
  font-size: 0.5em;
}
/* MEDIA QUERIES */
@media screen and (max-width: 1285px) {
  .cards-wrapper {
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (max-width: 900px) {
  .cards-wrapper {
    grid-template-columns: 1fr;
  }
  .projects {
    justify-content: center;
  }
}

@media screen and (max-width: 500px) {
  .cards-wrapper {
    padding: 2rem 1rem;
  }
  .card {
    max-width: calc(100vw - 2rem);
    font-size: 1.2em;
  }
}

@media screen and (max-width: 450px) {
  .projects {
    display: block;
    text-align: center;
  }
  .projects h1 {
    margin: 0;
    font-size: 1.5rem;
  }
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}

.main-tab {
  display: flex;
  flex-direction: column;
  padding: 1em;
  text-align: center;
}

.profile-image {
  display: inline-block;
}

.img-circle {
  border-radius: 50%;
  height: 450px;
  width: 450px;
}

.info {
  background-color: rgba(256,66,22,.1);
  border-radius: 50px;
  color: #3c3163;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 1em;
  padding: 1em;
}

.info h1 {
  font-family: 'Cinzel', serif;
}

.info h3 h4 {
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
}

.info i {
  font-size: 5rem;
  margin: 0 0.5rem;
}

.contact-tab {
  background-color: white;
  border-radius: 1rem;
  box-shadow: 0 0 5em -1em rgba(256,66,22,.2);
  max-width: 600px;
  margin: 0 auto;
  padding: 0 20px;
}

.contact-form { 
  max-width:420px; 
  margin:50px auto; 
  padding-top: 20px;
  padding-bottom: 50px;
}

.contact-form h1 {
  color: #3c3163;
  font-family: 'Cinzel', serif;
}

.contact-form input[type="email"], 
.contact-form input[type="text"],
.contact-form textarea {
  color:#3c3163;
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
  font-weight:500;
  font-size: 18px;
  border-radius: 5px;
  line-height: 22px;
  background-color: transparent;
  border:2px solid rgba(229,57,53,0.8);
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  padding: 13px;
  margin-bottom: 15px;
  width:100%;
  box-sizing: border-box;
  outline:0;
}

.contact-form input[type="email"]:focus, 
.contact-form input[type="text"]:focus,
.contact-form textarea:focus {
  border:2px solid rgba(213,0,125,0.8); 
}

.contact-form input[type="submit"] {
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
  width: 100%;
  background:-webkit-linear-gradient(45deg, rgba(213,0,125,0.8), rgba(229,57,53,0.8));
  background:linear-gradient(45deg, rgba(213,0,125,0.8), rgba(229,57,53,0.8));
  border-radius:5px;
  border:0;
  cursor:pointer;
  color:white;
  font-size:24px;
  padding-top:10px;
  padding-bottom:10px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  margin-top:-4px;
  font-weight:700;
}

.contact-form input[type="submit"]:hover { 
  background:-webkit-linear-gradient(45deg, rgba(229,57,53,0.8), rgba(213,0,125,0.8)); 
  background:linear-gradient(45deg, rgba(229,57,53,0.8), rgba(213,0,125,0.8)); 
}

@media (max-width: 768px) {
  .img-circle {
    border-radius: 0;
    max-width: 65%;
    height: auto;
  }
  .info {
    font-size: 85%;
  }
  .info i {
    font-size: 4rem;
  }
}

@media (max-width: 480px) {
  .img-circle {
    max-width: 80%;
  }
  .info {
    font-size: 70%;
  }
  .info i {
    font-size: 3rem;
  }
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

